import React, { Component, PropTypes } from 'react';
import UserBox from '../UserBox';
import Form from '../Form';
import './app-reviews.scss';
var axios = require('axios');

export default class AppReviews extends Component {
 constructor(props) {
  super(props);
  this.state = {
    reviews: []
  }

  this.handleSubmit = this.handleSubmit.bind(this);
 }

 componentDidMount() {
  axios.get('/reviews')
    .then(response => this.setState({
      reviews: response.data
    }))
 }

  handleSubmit(event) {
    axios.post('/reviews', this.state)
    .then(function (response) {
      console.log(response);
    })
    .catch(function (error) {
      console.log(error);
    });
    event.preventDefault();
  }

  render() {
    return (
      <div>
        <div className='reviews'>
          <h1>Reviews</h1>
          <p>See what our users have been saying.</p>
        </div>
        { this.renderReviews() }
        <Form onSubmit={this.handleSubmit} />
      </div>
    );
  }

  renderReviews() {
    const { reviews } = this.state;
    return reviews.map(rev => {
      return <UserBox name={ rev.title } comment={ rev.comment } photo={ rev.avatar } />
    })
  }
}



